import React, { useEffect, useState } from 'react';
import clsx from 'clsx';
import EventListener from 'react-event-listener';
import { AppState } from '../../reducers/app';

interface SailingScrollDownProps {
  text: string;
}

const SailingScrollDown: React.FC<SailingScrollDownProps & AppState> = ({
  text,
  scrollTop,
  windows
}) => {
  const [isFixed, setIsFixed] = useState(false);
  const [isFeature, setIsFeature] = useState(false);
  const handleScroll = () => {
    const { width, vw, vh } = windows;
    const isSmartPhone = width < 768;
    let headerHeight = 76;
    if (isSmartPhone) {
      headerHeight = 56;
    }

    const fixed = windows.height - 10 * vh - 173 - headerHeight;

    if (scrollTop >= fixed) {
      setIsFixed(true);
    } else {
      setIsFixed(false);
    }

    if (scrollTop - (86 + 4 * vh) >= fixed) {
      setIsFeature(true);
    } else {
      setIsFeature(false);
    }
  };

  return (
    <div
      className={clsx({
        sailingScrollDown: true,
        fixed: isFixed,
        feature: isFeature
      })}
    >
      <EventListener target="window" onScroll={handleScroll} />
      <span className="sailingScrollDown__txt">{text}</span>
      <span className="sailingScrollDown__arrow"></span>
    </div>
  );
};

export default SailingScrollDown;
